import {
  Box,
  Paper,
  Button,
  styled,
  TextField,
  IconButton,
  useTheme,
  useMediaQuery,
} from "@mui/material";
import { api } from "api/API";
import appStore from "app/mobxStore/AppStore";
import React, { useEffect, useState } from "react";
import StyledCardComponent from "../controlcentral/StyledCardComponent";
import { useNavigate } from "react-router-dom";
import { AssuredWorkloadOutlined, Search } from "@mui/icons-material";
import { Themecolors } from "api/Colors";
import CloseIcon from "@mui/icons-material/Close";
import Loading from "app/components/Loading";

interface DocumentType {
  documentGroupName: string;
  documentTypeId?: number;
  documentTypeName: string;
  documentTypeDescription: string;
  documentGroupId: number;
  documentTypeTableId: number;
}

const CreateForms: React.FC = () => {
  const navigate = useNavigate();
  const [documentTypes, setDocumentTypes] = useState<DocumentType[]>([]);
  const [groupedDocuments, setGroupedDocuments] = useState<
    Map<string, DocumentType[]>
  >(new Map());
  const [selectedGroups, setSelectedGroups] = useState<Set<string>>(new Set());
  const [searchTerm, setSearchTerm] = useState("");
  const [loading, SetLoading] = useState(true);

  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down("sm"));

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await api.get(
          `administrator/user/document-types/${appStore.loginResponse.user[0].userId}`
        );
        setDocumentTypes(response.data);
      } catch (error) {
        console.error("Error fetching document types:", error);
      } finally {
        SetLoading(false);
      }
    };
    fetchData();
  }, []);

  useEffect(() => {
    if (documentTypes.length > 0) {
      const actionMap: Map<string, DocumentType[]> = new Map();
      documentTypes.forEach((doc) => {
        const groupName = doc.documentGroupName;

        if (!actionMap.has(groupName)) {
          actionMap.set(groupName, []);
        }
        actionMap.get(groupName)?.push(doc);
      });
      setGroupedDocuments(actionMap);
    }
  }, [documentTypes]);

  const handleGroupToggle = (groupName: string) => {
    setSelectedGroups((prev) => {
      const updatedGroups = new Set(prev);
      if (updatedGroups.has(groupName)) {
        updatedGroups.delete(groupName);
      } else {
        updatedGroups.add(groupName);
      }
      return new Set(updatedGroups);
    });
  };
  const handleCall = (id: number, formTitle: string) => {
    navigate(`/user/forms/${id}?formTitle=${formTitle}`);
  };

  return (
    <Paper sx={{ height: "99%", width: "100%", p: 1 }}>
      <Box
        sx={{
          display: "flex",
          flexDirection: isMobile ? "column" : "row",
          alignItems: isMobile ? "flex-start" : "center",
          justifyContent: "space-between",
          flexWrap: "wrap",
          gap: 2,
          mb: 2,
          boxShadow: "0 2px 8px rgba(0, 0, 0, 0.1)",
          background: "linear-gradient(to bottom, #fafafa 0%, #ffffff 100%)",
          p: 2,
          borderRadius: 1,
        }}
      >
        <Box
          sx={{
            display: "flex",
            gap: 1,
            flexWrap: "wrap",
            justifyContent: isMobile ? "center" : "flex-start",
          }}
        >
          {Array.from(groupedDocuments.keys()).map((groupName) => (
            <Button
              key={groupName}
              type="button"
              onClick={() => handleGroupToggle(groupName)}
              variant={selectedGroups.has(groupName) ? "contained" : "outlined"}
              color="primary"
              sx={{
                height: "2.2em",
                display: "flex",
                alignItems: "center",
                gap: 0.5,
                fontWeight: selectedGroups.has(groupName) ? "bold" : "normal",
              }}
            >
              {groupName}
              {selectedGroups.has(groupName) && (
                <CloseIcon sx={{ fontSize: "1.3em" }} />
              )}
            </Button>
          ))}
        </Box>

        <TextField
          variant="filled"
          label="Search"
          style={{ margin: "2px" }}
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          InputProps={{
            endAdornment: (
              <IconButton sx={{ color: Themecolors.Dg_Icon2 }}>
                <Search />
              </IconButton>
            ),
          }}
          InputLabelProps={{
            sx: {
              color: Themecolors.Button2,
              transform: "translate(6px, 10px)",
              transition:
                "transform 0.2s ease-in-out, background-color 0.3s ease-in-out",
              "&.Mui-focused": {
                color: Themecolors.Button2,
              },
              "&.MuiInputLabel-shrink": {
                transform: "translate(5px, 0)",
                fontSize: "0.8em",
              },
            },
          }}
          sx={{
            width: isMobile ? "100%" : 250,
            height: "100%",
            backgroundColor: Themecolors.Button_bg4,
            "& .MuiInputBase-input": {
              height: 5,
            },
            "& .MuiInputBase-root": {
              backgroundColor: Themecolors.Button_bg3,
              display: "flex",
              alignItems: "center",
              height: "100%",
            },
            "&:hover .MuiInputBase-root": {
              backgroundImage: Themecolors.B_hv3,
            },
          }}
        />
      </Box>
      <>
        {loading ? (
          <Box
            sx={{
              height: "85%",
              width: "90%",
              display: "flex",
              justifyContent: "center",
              alignContent: "center",
              alignItems: "center",
            }}
          >
            <Loading />
          </Box>
        ) : (
          <Box
            sx={{
              display: "block",
              flexWrap: "wrap",
              gap: 2,
              mt: 2,
              px: 1,
              overflowY: "auto",
              overflowX: "hidden",
              height: "100%",
              maxHeight: "calc(95vh - 135px)",
            }}
          >
            {Array.from(
              selectedGroups.size > 0 ? selectedGroups : groupedDocuments.keys()
            ).map((groupName) => {
              const documents = groupedDocuments.get(groupName) || [];
              const filteredDocuments = documents.filter((doc) =>
                doc.documentTypeName
                  .toLowerCase()
                  .includes(searchTerm.toLowerCase())
              );

              if (filteredDocuments.length === 0) return null;

              return (
                <Box
                  key={groupName}
                  sx={{
                    width: "100%",
                    marginBottom: "10px",
                  }}
                >
                  <Box
                    sx={{
                      fontSize: "1em",
                      mb: 0.5,
                      px: 1,
                      py: 0.5,
                      backgroundColor: Themecolors.Dg_bg1,
                      borderRadius: 1,
                      width: "fit-content",
                    }}
                  >
                    {groupName}
                  </Box>
                  <Box
                    sx={{
                      display: "flex",
                      flexWrap: "wrap",
                      gap: 2,
                      my: 1,
                      justifyContent: isMobile ? "center" : "flex-start",
                    }}
                  >
                    {filteredDocuments.map((doc) => (
                      <StyledCardComponent
                        key={doc.documentTypeId}
                        title={doc.documentTypeName}
                        onClick={() =>
                          handleCall(doc.documentTypeId!, doc.documentTypeName)
                        }
                        icon={
                          <AssuredWorkloadOutlined sx={{ fontSize: "1em" }} />
                        }
                      />
                    ))}
                  </Box>
                </Box>
              );
            })}
          </Box>
        )}
      </>
    </Paper>
  );
};

export default CreateForms;
